.portfolioWrapper {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  /* justify-content: space-around; */
  display: grid;
  grid-template-columns: 50% 50%;
}

.portfolioImage {
  width: 300px;
  height: 400px;
}

@media screen and (max-width: 820px) {
  .portfolioWrapper {
    grid-template-columns: 100%;
    grid-template-rows: auto;
    gap: 45px;
    text-align: center;
    padding: 0 15px;
  }
  .portfolioImage {
    display: flex;
    align-self: center;
    justify-self: center;
  }
}
