* {
  box-sizing: border-box;
}
.page {
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.section {
  width: 100%;
  margin: 40px 250px;
  display: flex;
  flex-direction: column;
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  background-color: black;
  color: white;
  border: none;
  padding: 12px 24px;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: bold;
  letter-spacing: 2px;
}

.button:hover {
  background-color: grey;
}

h4 {
  font-size: 14px;
  margin: 0 0 24px 0;
  text-transform: uppercase;
  letter-spacing: 5px;
}

h3 {
  margin: 0 0 16px 0;
}

@media screen and (max-width: 820px) {
  .section {
    margin: 0;
  }
}
